
/* Styling buttons */

button {
    border-radius: 25px;
    font-size: 1.2em;
}

button:hover {
    background-color: rgb(192, 175, 18);
    transition: 0.5s;
    color: white;
}


/* Styling the Footer */

* {
    padding: 0;
    margin: 0;
}

body {
    background-color: rgb(245, 243, 243);
}

.main-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    color: rgb(34, 34, 34);
}

.footer-container {
    display: flex;
    flex-direction: row;
    margin-top: auto;
    background-color: white;
    align-items: center;
    justify-content: center;
    border-top: 1px solid;
    border-color: rgb(211, 211, 211);
    padding: 2rem
}

.footer-container h2 {
    line-height: 50px;
    margin: 0 1.5em 0 1.5em
}

.icons-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 35px;
    font-size: 35px;
    text-align: center;
}

.icons-container a {
    color: rgb(75, 75, 75);
    text-shadow: 5px 5px 5px black;
}

.icons-container a:hover {
    color: rgb(39, 39, 39);
    transition: 0.5s;
}

/* Styling navbar */
.nav-bar {
    height: 70px;
    width: 100%;
    background-color: rgb(192, 175, 18);
    color: rgb(37, 37, 37);
}

.logo {
    font-size: 2.5rem;
    color: white;
    text-decoration: none;
}

.logo-label {
    float: right;
    margin: 10px 50px 0 0;
}


@media (max-width: 952px) {
    .logo {
        font-size: 28px;
        margin-left: 25px;
    }
    .nav-bar {
        display: flex;
    }
}

/* Styling welcome */

.welcome {
    display: flex;
    gap: 1rem;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin: auto 150px auto 150px;
    padding: 50px;
    border: 1px solid;
    border-color: rgb(211, 211, 211);
    border-radius: 25px;
    background-color: rgb(255, 255, 255);
}

.welcome button {
    width: 120px;
    height: 35px;
    text-align: center;
}

/* Styling type page */

.type-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: auto 150px auto 150px;
    gap: 1rem
}
